import React from 'react'
import styled from 'styled-components'
const StyledRecipe = styled.div`
width: 260px;
padding: 10px;
display: flex;
flex-direction: column;
align-items: center;
border: 1px solid orange;
margin: 15px;
position: relative;
background-color: lightgrey;
`
const RecipeTitle = styled.div`
display: flex;
flex-wrap: wrap;
position: absolute;
top: 5;
z-index: 2;
background-color: orange;
width: 70%;
justify-content: center;
align-items: center;
font-size: 15px;
height: fit-content;
opacity: 0.85;
color: white;
padding: 10px;
`

const RecipeImage = styled.div`
position: relative;

img {
    height: 240px;
}
`

const RecipeUrl = styled.div`
`

const RecipeIngredients = styled.div`
`


const Recipe = ({recipe}) => {
    const {label, image, url, ingredients} = recipe.recipe;
    return (
        <StyledRecipe>
            <RecipeTitle>{label}</RecipeTitle>
            <RecipeImage><img src={image} alt={label}/></RecipeImage>
            <RecipeUrl>
            <a href={url} target="_blank" rel="noopener 
            noreferrer">URL</a>
            </RecipeUrl>
            <RecipeIngredients>Ingredients</RecipeIngredients>
        </StyledRecipe>
    )
}

export default Recipe
